.live-example {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: @darkgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  // outline: 2px solid red;
  // outline-offset: -2px;

  @content-shift: 179px; //Превышение контента на десктопе

  @media (min-width: @desktop-width) {
    flex-direction: row;
    align-items: stretch;

    align-self: center;
    width: 100vw;
    justify-content: center;
    background-color: @lightgray;
    margin-top: @content-shift;
  }

  &__wrapper-top {
    background-color: @lightgray-2;
    padding-top: 25px;
    padding-left: @padding-mobile;
    padding-right: @padding-mobile;
    display: flex;
    flex-direction: column;
    width: 100%;
    // outline: 1px dashed black;
    // outline-offset: -1px;


    @media (min-width: @tablet-width) {
      background-color: @white;
      border-top: 1px solid @specialgray-3;
      padding-left: @padding-tablet;
      padding-right: @padding-tablet;
      padding-bottom: 250px;
    }

    @media (min-width: @desktop-width) {
      padding: 43px 170px 35px 110px;
      background-color: transparent;
      box-sizing: border-box;
      width: calc(@desktop-width / 2);
      margin-top: -@content-shift;
      border: none;
    }
  }

  &__wrapper-bottom {
    background-color: @lightgray-2;
    padding-bottom: 40px;
    padding-left: @padding-mobile;
    padding-right: @padding-mobile;
    display: flex;
    flex-direction: column;
    width: 100%;
    // outline: 1px dashed black;
    // outline-offset: -1px;

    @media (min-width: @tablet-width) {
      padding-left: @padding-tablet;
      padding-right: @padding-tablet;
    }

    @media (min-width: @desktop-width) {
      background-color: transparent;
      box-sizing: border-box;
      width: calc(@desktop-width / 2);
      margin-top: -@content-shift;
      padding: 0;
    }
  }

  &__title {
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 40px;
    color: @black;
    margin: 0;
    margin-bottom: 40px;

    @media (min-width: @tablet-width) {
      font-size: 60px;
      line-height: 60px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 146px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;

    @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 24px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 69px;
    }
  }

  &__image {
    margin-bottom: 15px;
    align-self: center;
    height: 232px;

    @media (min-width: @tablet-width) {
      margin-top: -180px;
      height: 511px;
    }

    @media (min-width: @desktop-width) {
      margin: 0;
      height: 511px;
    }
  }

  &__range {
    align-self: center;
  }
}
