}
.control {
  position: relative;
  padding-left: 45px;
  cursor: pointer;
}
.control:hover .control__mark {
  border-color: #bababa;
}
.control__mark {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  box-sizing: border-box;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
.control__input:focus + .control__mark {
  outline: 2px solid #444444;
}
.control__input:disabled ~ * {
  opacity: 0.3;
  cursor: not-allowed;
}
.control__input[type="checkbox"]:checked + .control__mark {
  background-image: url("../../img/checkbox-checked-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.control__input[type="radio"] + .control__mark {
  border-radius: 50%;
}
.control__input[type="radio"]:checked + .control__mark::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  width: 14px;
  height: 14px;
  background-color: #68b738;
  border-radius: 50%;
}
