.features__text {
  margin: 0;
  padding-left: 80px;
  position: relative;
}
@media (min-width: 768px) {
  .features__text {
    padding-left: 0;
    padding-top: 151px;
    padding-bottom: 57px;
  }
  .features__text--organic::after,
  .features__text--simple::after,
  .features__text--easy::after,
  .features__text--quick::after {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
  }
  .features__text--organic::after {
    content: "1";
  }
  .features__text--simple::after {
    content: "2";
  }
  .features__text--easy::after {
    content: "3";
  }
  .features__text--quick::after {
    content: "4";
  }
}
.features__text--organic::before,
.features__text--simple::before,
.features__text--easy::before,
.features__text--quick::before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .features__text--organic::before,
  .features__text--simple::before,
  .features__text--easy::before,
  .features__text--quick::before {
