.range {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  display: flex;
  align-items: center;
}
.range__scale {
  position: relative;
  box-sizing: border-box;
  width: 86px;
  height: 14px;
  border: 1px solid #cdcdcd;
  border-radius: 6px;
  margin-left: 13px;
  margin-right: 13px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .range__scale {
    width: 426px;
    height: 6px;
    border: none;
    background-color: #cdcdcd;
    margin-left: 23px;
    margin-right: 23px;
  }
}
.range__toggle {
  position: absolute;
  box-sizing: border-box;
  padding: 0;
  width: 45%;
  height: 50%;
  border: none;
  background-color: #68b738;
  border-radius: 3px;
  cursor: pointer;
  right: 50%;
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .range__toggle {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    border: 1px solid #cdcdcd;
    transform: translateY(-50%) translateX(50%);
    background-color: #ffffff;
    filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));
  }
  .range__toggle::before {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #68b738;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
  }
  .range__toggle:hover {
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
  }
  .range__toggle:active {
    filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
    border-color: #68b738;
  }
}
.range__before,
.range__after {
  font: inherit;
  text-transform: inherit;
  border: none;
  cursor: pointer;
  padding: 0;
}
@media (min-width: 768px) {
  .range__before,
  .range__after {
    font-size: 20px;
    line-height: 30px;
    border-bottom: 1px dashed #cdcdcd;
  }
  .range__before:hover,
  .range__after:hover {
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  }
  .range__before:active,
  .range__after:active {
    border: none;
  }
}
